import React, { useState } from "react";
import { Box, useDisclosure } from "@chakra-ui/react";
import {
  FormControl,
  FormLabel,
  Grid,
  GridItem,
  Input,
  NumberInput,
  NumberInputField,
  NumberInputStepper,
  NumberIncrementStepper,
  NumberDecrementStepper,
  Select,
  Button,
  useToast,
} from "@chakra-ui/react";
import axios from "axios";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
} from "@chakra-ui/react";

function AddProduct({ getProduct }) {
  const toast = useToast();
  const [formData, setFormData] = useState({
    title: "",
    category: "",
    brand: "",
    price: "",
    discountPercentage: "",
    thumbnail: "",
    description: "",
  });

  const handleInputChange = (field, value) => {
    setFormData((prevData) => ({
      ...prevData,
      [field]: value,
    }));
  };

  const { isOpen, onOpen, onClose } = useDisclosure();

  const initialRef = React.useRef(null);
  const finalRef = React.useRef(null);

  const handleSubmit = () => {
    // You can do something with the formData object, e.g., send it to an API

    axios
      .post("/products", formData)
      .then((response) => {
        toast({
          title: "Success",
          description: response.data.message,
          status: "success",
          duration: 2000,
          isClosable: true,
        });
        getProduct();
        onClose();
        setFormData({
          title: "",
          category: "",
          brand: "",
          price: "",
          discountPercentage: "",
          thumbnail: "",
          description: "",
        });
      })
      .catch((error) => {
        console.log(error);
        toast({
          title: "Error Occured",
          description: error.response.data.error.message,
          status: "error",
          duration: 2000,
          isClosable: true,
        });
      });
  };

  return (
    <>
      <Button onClick={onOpen} color="blue" variant={"contain"}>
        Add Product
      </Button>
      <Modal
        initialFocusRef={initialRef}
        finalFocusRef={finalRef}
        isOpen={isOpen}
        onClose={onClose}
      >
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>Add Product</ModalHeader>
          <ModalCloseButton />
          <ModalBody pb={6}>
            <Box m={3}>
              <Grid templateColumns="repeat(2, 1fr)" gap={6}>
                <GridItem>
                  <FormControl isRequired>
                    <FormLabel>Title</FormLabel>
                    <Input
                      value={formData.title}
                      onChange={(e) =>
                        handleInputChange("title", e.target.value)
                      }
                      placeholder="Title"
                    />
                  </FormControl>
                </GridItem>

                <GridItem>
                  <FormControl isRequired>
                    <FormLabel>Category</FormLabel>
                    <Select
                      value={formData.category}
                      onChange={(e) =>
                        handleInputChange("category", e.target.value)
                      }
                      placeholder="Select Category"
                    >
                      <option>Laptop</option>
                      <option>Mobile</option>
                      <option>LCD</option>
                    </Select>
                  </FormControl>
                </GridItem>

                <GridItem>
                  <FormControl isRequired>
                    <FormLabel>Brand</FormLabel>
                    <Select
                      value={formData.brand}
                      onChange={(e) =>
                        handleInputChange("brand", e.target.value)
                      }
                      placeholder="Select Brand"
                    >
                      <option>Apple</option>
                      <option>Oppo</option>
                      <option>Vivo</option>
                      <option>Samsung</option>
                    </Select>
                  </FormControl>
                </GridItem>

                <GridItem>
                  <FormControl isRequired>
                    <FormLabel>Thumbnail</FormLabel>
                    <Input
                      value={formData.thumbnail}
                      onChange={(e) =>
                        handleInputChange("thumbnail", e.target.value)
                      }
                      placeholder="Paste url link here"
                    />
                  </FormControl>
                </GridItem>

                <GridItem>
                  <FormControl isRequired>
                    <FormLabel>Discout</FormLabel>
                    <NumberInput>
                      <NumberInputField
                        value={formData.discountPercentage}
                        onChange={(e) =>
                          handleInputChange(
                            "discountPercentage",
                            e.target.value
                          )
                        }
                      />
                      <NumberInputStepper>
                        <NumberIncrementStepper />
                        <NumberDecrementStepper />
                      </NumberInputStepper>
                    </NumberInput>
                  </FormControl>
                </GridItem>

                <GridItem>
                  <FormControl isRequired>
                    <FormLabel>Price</FormLabel>
                    <NumberInput>
                      <NumberInputField
                        value={formData.price}
                        onChange={(e) =>
                          handleInputChange("price", e.target.value)
                        }
                      />
                      <NumberInputStepper>
                        <NumberIncrementStepper />
                        <NumberDecrementStepper />
                      </NumberInputStepper>
                    </NumberInput>
                  </FormControl>
                </GridItem>

                <GridItem>
                  <FormControl isRequired>
                    <FormLabel>Description</FormLabel>
                    <Input
                      value={formData.description}
                      onChange={(e) =>
                        handleInputChange("description", e.target.value)
                      }
                      placeholder="description"
                    />
                  </FormControl>
                </GridItem>

                {/* <GridItem>
                  <FormLabel>Click To Submit</FormLabel>
                  <Button onClick={handleSubmit} colorScheme="blue">
                    Add Product
                  </Button>
                </GridItem> */}
              </Grid>
            </Box>
          </ModalBody>

          <ModalFooter>
            <Button onClick={handleSubmit} colorScheme="blue" mr={3}>
              Add
            </Button>
            <Button onClick={onClose}>Cancel</Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  );
}

export default AddProduct;
