import {
  Flex,
  Circle,
  Box,
  Image,
  Badge,
  useColorModeValue,
  Icon,
  chakra,
  Tooltip,
  Button,
  IconButton,
  ButtonGroup,
} from "@chakra-ui/react";
import { DeleteIcon, EditIcon } from "@chakra-ui/icons";

function ProductTemplate({
  title,
  thumbnail,
  price,
  brand,
  _id,
  deleteProduct,
  description
}) {
  return (
    <Flex p={50} w="full" alignItems="center" justifyContent="center">
      <Box
        bg={useColorModeValue("white", "gray.800")}
        maxW="sm"
        borderWidth="1px"
        rounded="lg"
        shadow="lg"
        position="relative"
      >
        <Image
          src={thumbnail}
          alt={`Picture of ${title}`}
          roundedTop="lg"
          style={{ height: "200px" }}
        />

        <Box p="6">
          <Box display="flex" alignItems="baseline"></Box>
          <Flex mt="1" justifyContent="space-between" alignContent="center">
            <Box
              fontSize="2xl"
              fontWeight="semibold"
              as="h4"
              lineHeight="tight"
              isTruncated
            >
              {title}
            </Box>
            <Tooltip
              label="Add to cart"
              bg="white"
              placement={"top"}
              color={"gray.800"}
              fontSize={"1.2em"}
            >
              <chakra.a href={"#"} display={"flex"}>
                {/* <Icon as={FiShoppingCart} h={7} w={7} alignSelf={"center"} /> */}
              </chakra.a>
            </Tooltip>
          </Flex>

          <Flex justifyContent="space-between" alignContent="center">
            {brand}
            <Box fontSize="2xl" color={useColorModeValue("gray.800", "white")}>
              <Box as="span" color={"gray.600"} fontSize="lg">
                £
              </Box>
              {price}
            </Box>
          </Flex>
          {/* <Flex justifyContent="space-between" alignContent="center">
            {description}
          </Flex> */}
        </Box>
        <ButtonGroup
          style={{
            display: "flex",
            justifyContent: "flex-end",
            padding: "5px",
          }}
          gap="2"
        >
          {/* <IconButton
            variant="outline"
            colorScheme="green"
            icon={<EditIcon />}
          /> */}
          <IconButton
            colorScheme="red"
            onClick={() => deleteProduct(_id, title)}
            icon={<DeleteIcon />}
          />
        </ButtonGroup>
      </Box>
    </Flex>
  );
}

export default ProductTemplate;
