import axios from "axios";
import React, { useEffect, useState } from "react";
import ProductTemplate from "./ProductTemplate";
import { Grid, GridItem, useToast } from "@chakra-ui/react";
import AddProduct from "./AddProduct";

function ShowProducts() {
  const [products, setproducts] = useState();
  const toast = useToast();

  function getProduct() {
    axios.get("/products").then((response) => {
      setproducts(response.data);
    });
  }

  function deleteProduct(_id, title) {
    let id = _id;
    if (window.confirm(`Are You sure want to delete ${title} from list ?`)) {
      axios.delete("/products/" + id).then((response) => {
        toast({
          title: "Deleted Successfully",
          status: "success",
          duration: 2000,
          isClosable: true,
        });
        getProduct();
      });
    }
  }

  useEffect(() => {
    getProduct();
  }, []);

  return (
    <>
      <div
        style={{
          width: "100%",
          padding: "10px",
          display: "flex",
          justifyContent: "end",
          alignItems: "end",
        }}
      >
        <AddProduct getProduct={getProduct} />
      </div>
      <Grid templateColumns="repeat(3, 1fr)" gap={2}>
        {products?.map((product) => (
          <GridItem w="100%">
            <ProductTemplate {...product} deleteProduct={deleteProduct} />
          </GridItem>
        ))}
      </Grid>
    </>
  );
}

export default ShowProducts;
